.ivu-timeline {
  list-style: none;
  margin: 0;
  padding: 0;
}
.ivu-timeline-item {
  margin: 0 !important;
  padding: 0 0 12px;
  list-style: none;
  position: relative;
}
.ivu-timeline-item-tail {
  height: 100%;
  border-left: 1px solid #e9eaec;
  position: absolute;
  left: 6px;
  top: 0;
}
.ivu-timeline-item-pending .ivu-timeline-item-tail {
  display: none;
}
.ivu-timeline-item-head {
  width: 13px;
  height: 13px;
  background-color: #fff;
  border-radius: 50%;
  border: 1px solid transparent;
  position: absolute;
}
.ivu-timeline-item-head-blue {
  border-color: #2d8cf0;
  color: #2d8cf0;
}
.ivu-timeline-item-head-red {
  border-color: #ed3f14;
  color: #ed3f14;
}
.ivu-timeline-item-head-green {
  border-color: #19be6b;
  color: #19be6b;
}
.ivu-timeline-item-head-custom {
  width: 40px;
  height: auto;
  margin-top: 6px;
  padding: 3px 0;
  text-align: center;
  line-height: 1;
  border: 0;
  border-radius: 0;
  font-size: 14px;
  position: absolute;
  left: -13px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.ivu-timeline-item-content {
  padding: 1px 1px 10px 24px;
  position: relative;
  top: -3px;
}
.ivu-timeline-item:last-child .ivu-timeline-item-tail {
  display: none;
}
.ivu-timeline.ivu-timeline-pending .ivu-timeline-item:nth-last-of-type(2) .ivu-timeline-item-tail {
  border-left: 1px dotted #e9eaec;
}
.ivu-timeline.ivu-timeline-pending .ivu-timeline-item:nth-last-of-type(2) .ivu-timeline-item-content {
  min-height: 48px;
}
.center .profile {
  text-align: center;
  position: relative;
}
.center .profile .banner {
    background: url("../../assets/img/center-bg.png") no-repeat;
    background-size: 100% 100%;
    height: 125px;
}
.center .profile .info {
    position: absolute;
    top: 1;
    width: 100%;
    z-index: 111;
    text-align: center;
}
.center .profile .info .img {
      width: 189px;
      height: 189px;
      border-radius: 50%;
      margin-top: 25px;
}
.center .profile .info .name {
      font-size: 30px;
      color: #171616;
      position: relative;
      top: -44px;
}
.center .profile .info .name .svg-icon {
        vertical-align: baseline;
        margin: 0 0 -1px  10px;
        display: inline-block;
}
.center .profile .info .upload-btn {
      background: #FFFFFF;
      position: relative;
      top: -45px;
      left: 59px;
      border: 1px solid rgba(0, 0, 0, 0.1);
      border-radius: 22.5px;
      width: 45px;
      margin: 0 auto;
      height: 45px;
      overflow: hidden;
      line-height: 52px;
}
.center .profile .info .upload-btn input {
        position: absolute;
        opacity: 1;
        right: 0;
        zoom: 20;
}
.center .profile .info .upload-btn .svg-icon {
        color: #6BC0B8;
}
.my-menu {
  margin-top: 200px;
}
.my-menu .svg-icon {
    vertical-align: middle;
}
.my-menu .mint-cell-text {
    font-size: 17px;
    color: #1F2D3D;
    margin-left: 5px;
}
.login-form {
  text-align: center;
}
.login-form .mobile-field .mint-cell-title {
    width: 70px;
}
.login-form .mobile-field .mint-cell-text {
    color: #999;
}
.footer {
  margin-top: 24px;
  text-align: center;
}
.footer .btn-submit {
    width: 90%;
    margin: 15px auto;
    background: #6BC0B8;
}
.gender-wrap {
  text-align: left;
}
.gender-wrap label input {
    -webkit-appearance: none;
    border: 1px solid #6BC0B8;
    display: inline-block;
    position: relative;
    width: 22px;
    height: 22px;
    box-sizing: border-box;
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    border-radius: 50%;
    vertical-align: middle;
}
.gender-wrap label input:checked {
      background: transparent;
}
.gender-wrap label input:checked:after {
        content: ' ';
        position: absolute;
        width: 12px;
        height: 12px;
        border-radius: 50px;
        top: 4px;
        left: 4px;
        background: #6BC0B8;
}
.gender-wrap input {
    zoom: 1.5;
    -webkit-appearance: none;
    display: block;
}
.gender-wrap input:checked {
      background: #5a9ae4;
}
.gender-wrap label {
    vertical-align: middle;
    margin-right: 28px;
}
.fields {
  padding: 0 40px;
}
.fields .mint-cell-wrapper {
    background-image: none;
    padding: 0;
}
.fields .mint-field-core {
    background: #f5f5f5;
    border: 1px solid #ebf1fa;
    height: 50px;
    border-radius: 4px;
    margin-bottom: 10px;
    padding: 0 10px;
}
.fields .mint-field-core:focus {
      background: #f5f5f5;
      border: 1px solid #5a9ae4;
      box-shadow: 0 0 6px 0 rgba(73, 143, 225, 0.41);
}
.fields .profile {
    margin: 40px 0 0 0;
}
.fields .profile:before, .fields .profile:after {
      content: " ";
      display: table;
}
.fields .profile:after {
      clear: both;
}
.fields .profile .name {
      font-size: 20px;
      margin: 2px 0 5px 0;
}
.fields .profile img {
      border-radius: 5px;
      float: left;
      margin-right: 10px;
}
.fields .notice {
    color: #999;
    margin: 36px 50px 30px 0;
}
.fields .notice p {
      margin-bottom: 10px;
}
.fields .notice li {
      list-style: none;
}
.age-field {
  text-align: center;
}
.age-field > div {
    font-size: 12px;
    color: #8492A6;
    border-bottom: 1px solid #E0E6ED;
    margin: 30px 25%;
    position: relative;
    text-align: center;
}
.age-field > div > span {
      position: absolute;
      background: #fff;
      padding: 0 3px;
      bottom: -8px;
      left: 50%;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
}
.age-field li {
    background: #C0CCDA;
    border-radius: 4px;
    width: 60px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    margin: 0 15px 15px 0;
    font-size: 16px;
    color: #FFFFFF;
    display: inline-block;
}
.age-field li:nth-of-type(4n) {
      margin-right: 0;
}
.age-field li.active {
      background: #6BC0B8;
}
.loving {
  background: #F5F5F5;
  min-height: 100%;
}
.loving .select {
    padding-top: 20px;
}
.loving .select .mint-cell {
      min-height: 68px;
}
.loving .select .mint-cell .mint-cell-label {
        margin-top: 10px;
        font-size: 14px;
}
.loving .select .mint-cell .mint-cell-text {
        font-size: 17px;
}
.loving.select-item li {
    height: 50px;
    line-height: 50px;
    padding: 0 15px;
    background: #fff;
    border-bottom: 1px solid #d7d7d7;
}
.loving.select-item li:before, .loving.select-item li:after {
      content: " ";
      display: table;
}
.loving.select-item li:after {
      clear: both;
}
.loving.select-item li:last-of-type {
      border-bottom: 0;
}
.loving.select-item li .svg-icon {
      float: right;
      margin-top: 24px;
}
.loving .timeline {
    background: #fff;
    padding: 20px 27px 0 27px;
    margin-top: 20px;
}
.loving .timeline .time {
      color: #8492A6;
}
.loving .timeline .content {
      font-size: 14px;
      color: #8492A6;
      letter-spacing: 0;
      margin-top: 10px;
      line-height: 24px;
}
.loving .timeline .content.active {
        color: #333;
}
.loving .care-notice {
    position: absolute;
    bottom: 20px;
    text-align: center;
    width: 100%;
    color: #999;
}
.coupon-detail li {
  margin: 5px 0;
}



